* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  font-size: 18px;
  font-family: "Noto Sans KR", Helvetica, Arial, sans-serif;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

pre {
  font-family: "Noto Sans KR", Helvetica, Arial, sans-serif;
}

@mixin removeDeco {
  text-decoration: none;
  color: #212121;
}

@keyframes reflection {
  from {
    background-position: 0% 100%;
  }
  to {
    background-position: 200% 100%;
  }
}
/////////////////////////////////////////////////////////////

.sidebar {
  float: left;
  height: 100vh;
  width: 300px;
  position: fixed;
  overflow: hidden;
  background-color: rgb(231, 244, 255);
  box-shadow: 5px 5px 13px #bdbdbd;

  display: flex;
  flex-flow: column nowrap;

  .profile {
    flex-basis: 250px;
    @include removeDeco();
    background-image: url("/assets/img/pier-569314_640.jpg");
    background-position: center;

    display: flex;
    flex-flow: column wrap;
    justify-content: space-around;
    align-items: center;

    .img {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      box-shadow: 1px 1px 15px #bdbdbd;
    }

    .text {  
      width: 250px;
      flex-basis: fit-content;
      background-color: rgba(255, 255, 255, 0.6);
      border-radius: 5%;
      text-align: center;
      .author {
        color: rgb(119, 136, 153);
        font-size: 1.5rem;
        font-weight: bold;
      }
      .description {
        font-size: 1rem;
      }
    }

  }

  .search {
    position: relative;

    .search-btn {
      border: none;
      width: 100%;
      height: 60px;
      background-color: #eeeeee;
      padding: 0 10px;

      &:focus {
        outline: none;
      }
    }
    .search-icon {
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  .navigation {
    .categories-container {
      .text {
        font-size: 1.5rem;
      }

      .categories { //ul
      
        .category { //li
          border-bottom: 2px solid black;
          padding: 10px 0;
          display: flex;
          justify-content: flex-start;
          transition: background-color 0.5s ease-in-out;

          i {
            text-align: center;
            flex-basis: 40px;
            cursor: pointer;
            font-size: 1.5rem;
          }
          a {
            flex-grow: 1;
            @include removeDeco();
          }
          &:hover {
            background-color: rgb(189, 218, 241);
          }
        }
      }
  
      .subnav { //ul
        transition: opacity 1s ease-out;
        opacity: 1;
        height: auto;
        padding-left: 25px;
        padding: 0 0;
      }
      .subnav--hide {
        opacity: 0;
        height: 0;
        overflow: hidden;
      }
    }
  }  
}

.page {
  float: right;
  width: calc(100% - 300px);
  min-height: 100vh;
  overflow: hidden;

  display: flex;
  flex-flow: column wrap;

  //clear
  &::after {
    content: "";
    display: block;
    clear: both;
  }

  @at-root .header--hide {
    transform: translateY(-50px);
    -moz-transform: translateY(-50px);
    -ms-transform: translateY(-50px);
    -o-transform: translateY(-50px);
    -webkit-transform: translateY(-50px);
  }
  .header {
    width: calc(100% - 300px);
    position: fixed;
    transition: transform 0.5s ease-in-out;
    -moz-transition: -moz-transform 0.5s ease-in-out;
    -o-transition: -o-transform 0.5s ease-in-out;
    -webkit-transition: -webkit-transform 0.5s ease-in-out;
    z-index: 1;

    display: flex;
    flex-flow: column wrap;

    .bar {
      background-color: rgba(255, 255, 255, 0.8);

      transition: background-color 0.6s ease-in-out;
      -moz-transition: background-color 0.6s ease-in-out;
      -o-transition: background-color 0.6s ease-in-out;
      -webkit-transition: background-color 0.6s ease-in-out;
      flex-basis: 50px;

      display: grid;
      grid-template-columns: 60px auto 60px;
      grid-template-rows: 50px;
      align-items: center;

      .hamburger {
        margin: auto;
        font-size: 22px;
      }

      .title {
        @include removeDeco();
        text-align: center;

        .main-title {
          font-size: 1rem;
        }

        .sub-title {
          font-size: 0.7rem;
        }
      }

      .logo {
        margin: auto;
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }
    }

    .scroll-indicator {
      flex-basis: 5px;

      transition: width 0.1s linear;
      -moz-transition: width 0.1s linear;
      -o-transition: width 0.1s linear;
      -webkit-transition: width 0.1s linear;

      width: 0%;
      background-color: rgba(4, 154, 192, 0.5);
    }
  }

  .main {
    flex: 1;
    @import "page", "post", "index";
  }

  .footer {
    .inner {
      padding: 0 30px;
      text-align: center;
    }
  }
}

/////////////////////////////////////////////////
@media screen and (max-width: 1068px) {
  .sidebar {
    visibility: hidden;
  }

  .page,
  .page .header {
    width: 100%;
  }
}
